.serchbar{
    background-color: black;
    color: white;
    /* height: 66px; */
    background-color: white;
    border-radius: 3rem;
    width: 90%;
    margin-left: 3rem;
    /* margin-right:  3rem; */
}
.serachbar-container{
    width: 100%;
    height: 66px;
    display: flex;
    color: black;
    justify-content: space-between;
    align-items: center;
}
.serachbar-container-item1{
    /* margin: 0; */
    padding-left: 1rem;
    /* padding-top: 0.6rem; */
    /* padding-bottom: 0.6rem; */
    border-right: 1px solid rgba(128, 128, 128, 0.256);
}
.serachbar-container-item1:hover{
    background-color: rgba(128, 128, 128, 0.2);
    border-radius: 20%;
    cursor: pointer;
}
.grey-class{
    color: grey;
}
.serachbar-container-item2{
    display: flex;
    justify-content: space-between;
    margin-left: 1rem;
    padding: 0.5rem 0.2rem;
}
.serachbar-container-item2:hover{
    background-color: rgba(128, 128, 128, 0.2);
    border-radius: 20%;
    cursor: pointer;

}
.serch-icon{
    margin-left: 6rem;
    font-size: 1.3rem;
     padding: 0.6rem 0.8rem;
    background-color: #ff385c;
    border-radius: 50%;
    color: white;
}
@media only screen and (max-width : 950px ){

}
@media only screen and (max-width : 708px ){
    .serchbar{
        display: none;
    }
}