.section1-wrapper{
    background-color: black;
    color: white;
    margin-left: 3rem;
    margin-right: 3rem;
    /* width: 100%; */
    display: flex;
    margin-top: 4rem;
    align-items: center;
    justify-content: center;
    background-color: #ff385c;
    border-radius: 1rem;
}
.card-container{
    height: 370px;
    width: 80%;
    display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.big-text{
    font-size: 2.2rem;
    font-weight: bold;
}
.center-btn{
    width: 100%;
    text-align: center;
}
.learn-btn{
    display: inline-block;
    padding: 0.7rem;
    border: white 1px solid;
    background-color: #ff385c;
    outline: none;
    margin-top: 1rem;
    color: white;
    opacity: none;
    border-radius: 0.5rem;
}
.learn-btn:hover{
    cursor: pointer;
}
@media only screen and (max-width : 708px ){
    .section1-wrapper{
        margin-top: 1rem;

    }
}