body{
    /* position: absolute; */
}
.header{
    height: 80px !important;
    margin: 0 auto;
    padding-left: 3rem;
    padding-right: 3rem;
    background-color: black;
    /* position: absolute;
    top: 0%;
    left: 0;
    width: 100%; */
}
.header_container{
    display: flex;
    justify-content: space-between;
    /* flex-wrap: wrap; */
}
.airnb_logo{
    height: 80px;
    width: auto;
}
.airnb_logo:hover{
    cursor: pointer;
}
.margin_for_mobile{
    height: 1rem;
    background-color: black;
    display: none;
}
@media only screen and (max-width : 708px ){
    .header_container{
        display: none;
    }
    ..header{
        padding-top: 1rem;
    }
    .margin_for_mobile{
        height: 1rem;
        background-color: black;
        display: block;
    }
}