.section2{
    color: white;
    width: 100%;
}
.section-container{
    margin-top: 2rem;
    margin-bottom: 2rem;
    margin-left: 3rem;
    position: relative;
}
.section2-image{
    /* height: auto; */
    width: 90%;
    height: 550px;
    /* margin-left: 3rem; */
    /* margin-right: 3rem; */
    border-radius: 2rem;
   
}
.curious{
    font-size: 2rem;
}

.position_this{
    position: absolute;
    /* top: 0%; */
    display: flex;
    flex-direction: column;
    bottom: 20%;
    left: 3%;
}
.flexible-btn{
    padding: 0.7rem 0.6rem;
    border-radius: 2rem;
    background-color:#fff;
    display: inline;
    width: 20%;
    position: absolute;
    top: 90%;
    margin-top: 0.5rem;
    left: 40%;
    color:  grey;

}
.position_this:hover{
    cursor: pointer;
}