.end-header{
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
}
       
.user_menu{
    display: flex;
    background-color: white;
    color: grey;
    padding-top: 0.2rem;
    /* padding-bottom: 0.4rem; */
    padding-right: 0.4rem;
    font-size: 1.5rem;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 1.5rem;
    border-bottom-right-radius: 1.5rem;
    border-bottom-left-radius: 1.5rem;
}
.user_menu:hover{
    cursor: pointer;
}
.end-header-items{
    margin-left: 1rem;
    /* padding: 0.5rem; */
}
.user-items{
    margin-left: 1rem ;
}
 
.user-item1{
    font-size: 1.1rem;
}
.user-item2{
    font-size: 1.9rem;
}
.world-item{
    font-size: 1.5rem;
}
.end-header-item1{
    padding: 0.5rem;
}
.end-header-item2{
    padding: 0.5rem;
}
.end-header-item2:hover{
    background-color: rgba(128, 128, 128, 0.636);
    border-radius: 1rem;
    cursor: pointer;
}
.end-header-item1:hover{
    background-color: rgba(128, 128, 128, 0.645);
    border-radius: 1rem;
    cursor: pointer;
}