.mid_header_web{
    display: flex;
    color: white;
    justify-content: space-around;
    align-items: center;
}
.mid_header_items{
    margin-left: 1rem;
    padding: 1rem;
}
.mid_header_items:hover{
    cursor: pointer;
    border-bottom: 1px solid white;
   
}
 
.mid_header_items:hover{
    color: grey;
}
@media only screen and (max-width : 950px ){
    .mid_header_web{
        display: none;
    }
}